:host.home-banner-nav-items {
	a.head-link {
		color: var(--basic-1) !important;
	}
}

a.item-link {
	width: 280px !important;
	min-height: 45px;
	color: var(--general-5);

	&:not(:last-child) {
		border-bottom: 1px solid var(--neutral-1);
	}
}

a.head-link-active {
	border-bottom: 3px solid var(--prim-2);
	border-radius: 0;
	color: var(--prim-2) !important;

	transition: 0.2s ease-in-out;
}

.new-sup {
	right: 3rem;
	position: relative;
	top: -15px;
}
